html {
    @include media-breakpoint-down(sm) {
        font-size:              0.91rem;
    }
}

.c-main {
    padding-bottom: 3rem;
}

// Here you can add other styles
.c-sidebar {
    background: #2a6cb5;
}

.c-app:not(.c-legacy-theme):not(.c-dark-theme) .card {
    //box-shadow: 0 0 5px 0 rgba(60, 75, 100, .4);
    border: 1px solid #cddbdf;
    box-shadow: none;
}

.card-header {
    font-weight: 600;

    @include media-breakpoint-up(lg) {
        font-size: larger;
    }
}

hr {
    border-color: #dce5e7;
}

.input-group-text {
    border-color: #acc2c9;
}

.close {
    color: inherit;
    text-shadow: none;

    &:hover {
        color: inherit;
    }
}

.form-control {
    border-color: #acc2c9;
    border-width: 2px;
}

.form-label {
    font-weight: 600;

    @include media-breakpoint-up(lg) {
        font-size: 1rem;
    }
}

.btn-light {
    border: 1px solid #c6d0d3 !important;
}

.nav-tabs-boxed .nav-tabs {
    @include media-breakpoint-down(sm) {
        flex-wrap: nowrap;
        overflow-x: scroll;
        position: relative;
        background: linear-gradient(282deg, rgba(0, 0, 0, .15) 0, rgba(0, 0, 0, 0) 10px, rgba(0, 0, 0, 0)),
        linear-gradient(0deg, rgba(0,0,0, 0.1) 0, rgba(0, 0, 0, 0) 5px, rgba(0, 0, 0, 0));
    }

    .form-check {
        @include media-breakpoint-down(sm) {
            white-space: nowrap;
        }
    }
}

.nav-tabs .nav-item {
    @include media-breakpoint-down(sm) {
        white-space: nowrap;
    }
}

.nav-tabs-boxed .tab-content {
    border-color: #c6d0d3;
}

.nav-tabs > li > .nav-link.active {
    color: #333;
    font-weight: 400;
    border-color: #c6d0d3;
}

.nav-tabs > li > a:hover {
    background: rgba(#000,0.05);
    border-color: transparent;
}

.nav-tabs-boxed .tab-content {
    color: inherit;
}

a[data-toggle=collapse] {
    display: inline-flex;
    align-items: center;

    &:before {
        width: 12px;
        height: 12px;
        font-size: 12px;
        color: #768192;
        border: 1px solid #768192;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-right: 5px;
        line-height: 1;
    }

    &[aria-expanded=false] {
        &:before {
            content: "＋";
        }
    }

    &[aria-expanded=true] {
        &:before {
            content: "-";
        }
    }
}

.mark-lamp {
    width: 8px;
    height: 8px;
    border-radius: 999px;
    display: inline-block;
    background: #283ad6;
}

.help-button {
    font-size: 14px;
    width: 16px;
    height: 16px;
    background: #869aac;
    border-radius: 999px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-weight: 500;
    color: #fff;
    margin: 0 2px;
}

.ck-editor__editable {
    @include media-breakpoint-up(sm) {
        min-height: 200px;
    }

    @include media-breakpoint-down(sm) {
        min-height: 150px;
    }
}

//サイドバー幅を小さく

html:not([dir="rtl"]) .c-sidebar.c-sidebar-lg-show:not(.c-sidebar-right).c-sidebar-fixed ~ .c-wrapper, html:not([dir="rtl"]) .c-sidebar.c-sidebar-show:not(.c-sidebar-right).c-sidebar-fixed ~ .c-wrapper {
    @include media-breakpoint-up(lg) {
        margin-left: 230px;
    }
}


.c-sidebar {
    @include media-breakpoint-up(md) {
        width: 230px;
    }

    &.bg-trans {
        background: rgba(#fff, 0.6);

        @include media-breakpoint-down(sm) {
            width: 250px;
        }

        .nav {
            background: #fff;
        }
    }
}

.fixed-footer {
    width: 100%;
    padding: 20px 20px 0 250px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#ebedef,0.8);
    z-index: 5;

    @include media-breakpoint-down(sm) {
        padding: 15px 15px 0;
    }
}

.down-arrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 30px 100px 0 100px;
    border-color: #ccc transparent transparent transparent;
}

.c-switch-input:not(:checked) ~ .c-switch-slider  {
    background: #999;

    &:before {
        border-color: #999;
    }

    &:after {
        color: #fff;
    }
}

.table-scroll-sm {
    position: relative;

    th,td {
        @include media-breakpoint-down(sm) {
            white-space: nowrap;

            .card {
                white-space: initial;
            }
        }
    }
}

.progress {
    background: #d6d6d6;
}


.modal-dialog {
    @include media-breakpoint-up(lg) {
        max-width: 600px;
    }
}


.sortable-tr {
    background: #2a6cb5 !important;
    color: #fff;

    a {
        color: #fff;

        &:before {
            color: #fff;
            border-color: #fff;
        }
    }
}

.table-nested {
    & > thead > tr > th {
        border-width: 1px !important;

    } 

    & > tbody > tr > td {
        border-top: none !important;

        table {
            border: none !important;

            th {
                border: none;
            }

            td {
                border-left: none;
            }
        }
    }
}

.brand-login {
    @include media-breakpoint-up(md) {
        width: 70%;
        margin-bottom: 30px;
    }

    @include media-breakpoint-down(sm) {
        width: 60%;
        margin: 20px 0;
    }
}

.form-check-inline {
    &.is-address {
        align-items: flex-start;

        @include media-breakpoint-up(md) {
            margin-right: 1.5rem !important;
        }

        @include media-breakpoint-down(sm) {
            margin-right: 0 !important;
            margin-left: 1rem;
            
            & + & {
                margin-top: 1.5rem;
            }
        }

        .form-check-input {
            margin-top: 5px;
        }
    }
}

.scroll-hint-icon {
    z-index: 5;
}

.scroll-y {
    overflow-y: scroll;
    max-height: 8em;
}

