// Variable overrides


//$enable-shadows: true;

$white:    #fff !default;
$gray-base:  #4c586e !default;
$gray-100: #f8f9fa !default;
$gray-200: #ecf0f1 !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #b4bcc2 !default;
$gray-600: #95a5a6 !default;
$gray-700: #7b8a8b !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:    #000 !default;

//$blue:    #2C3E50 !default;
$blue:    #2a6cb5 !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #E74C3C !default;
$orange:  #fd7e14 !default;
$yellow:  #F39C12 !default;
$green:   #18BC9C !default;
$teal:    #20c997 !default;
$cyan:    #2e90d1 !default;

$primary:       $blue !default;
$secondary:     $gray-800 !default;
$success:       $green !default;
$info:          $cyan !default;
$warning:       $yellow !default;
$danger:        $red !default;
$light:         $gray-200 !default;
$dark:          $gray-700 !default;

$theme-colors: (
  "secondary": $secondary
);

$yiq-contrasted-threshold:  175 !default;

// Body

$body-bg:                   $gray-100 !default;

// Links

$link-color:                $info !default;

// Tables
$table-head-color: $gray-800;
$table-hover-bg:      rgba($black, .04) !default;
$table-border-color:          #c6d0d3 !default;

// Cards

$card-border-color : #c6d0d3 !default;


